.main__first-box {
  width: 100%;
  height: 870px;
  background: url("image1-1.d8e2535c.webp") center / cover no-repeat;
  margin-top: 48px;
  display: flex;
  position: relative;
}

.main__first-box .main__first-box-card {
  background-color: #141414cc;
  flex-direction: column;
  padding: 20px;
  display: flex;
  position: absolute;
  bottom: calc(50% - 90px);
  left: 60px;
}

.main__first-box .main__first-box-card h2 {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 3px;
  font-size: 30px;
  font-weight: bold;
}

.main__first-box .main__first-box-card h4 {
  color: #fff;
  letter-spacing: 1px;
  margin-top: 18px;
  font-size: 14px;
}

.main__first-box .main__first-box-card button {
  text-transform: uppercase;
  color: #fff;
  letter-spacing: 1px;
  background-color: #3c3c3b;
  border: 1px solid #3c3c3b;
  margin: 24px auto 0 0;
  padding: 15px 20px 12px 50px;
  font-size: 12px;
  font-weight: bold;
  transition-duration: .5s;
  position: relative;
}

.main__first-box .main__first-box-card button:hover {
  background-color: #000;
  border: 1px solid #fff;
}

.main__first-box .main__first-box-card button .fa-arrow-right-long {
  font-size: 18px;
  display: inline-block;
  position: absolute;
  bottom: 10px;
  left: 20px;
}

@media screen and (max-width: 900px) {
  .main__first-box {
    height: 490px;
  }

  .main__first-box .main__first-box-card {
    left: 20px;
  }

  .main__first-box .main__first-box-card h2 {
    font-size: 20px;
  }
}

@media screen and (max-width: 740px) {
  .main__first-box {
    height: 450px;
    background-position: 50% -100px;
  }

  .main__first-box .main__first-box-card {
    width: 100%;
    text-align: center;
    background-color: #141414;
    align-self: end;
    position: static;
  }

  .main__first-box .main__first-box-card h4 {
    font-size: 16px;
  }

  .main__first-box .main__first-box-card button {
    margin: 24px auto 15px;
  }
}

@media screen and (max-width: 440px) {
  .main__first-box {
    height: 350px;
    background-position: 50% -100px;
  }
}

